.modal__message {
  width: 320px;
  min-height: 280px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.modal__message--failure {
  height: 280px;
}

.modal__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  margin: 0;
  margin-top: 45px;
}

.modal__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  text-align: center;
  margin: 39px 15px 0 36px;
}

.modal__button-container--success {
  width: 320px;
  height: 98px;
  background: #F2F2F2;
  margin-top: 30px;
}

.modal__button--success {
  width: 280px;
  height: 58px;
  color: #ffffff;
  margin: 20px 20px 20px 20px;
}

.modal__button--failure {
  width: 180px;
  height: 58px;
  color: #ffffff;
  margin-top: 28px;
}
